import { PromiseDebouncer } from '../../libs/PromiseDebouncer';

/* eslint-disable import/prefer-default-export */

/**
 * An event service that provides event data.
 */
export class EventService {
    constructor(args) {
        if (!args) {
            throw new Error(
                `args is required when instantiating a new EventService`
            );
        }

        [`eventAdapters`].forEach((name) => {
            if (!args[name]) {
                throw new Error(
                    `${name} property is required when instantiating a new EventService`
                );
            }
        });

        this.promiseDebouncer = new PromiseDebouncer();
        this.eventAdapters = args.eventAdapters;
    }

    /**
     * Get a list of events.
     * @param {object} getEventsDto The events dto.
     * @returns {Promise<array>} A promised list of events.
     */
    getEvents(getEventsDto) {
        return Promise.all(
            this.eventAdapters.map((adapter) => {
                return this.promiseDebouncer.debounce(
                    {
                        name: adapter.sourceId, // Include the sourceId of the adapter so the debounce can tell the requests apart if multiple sources exist
                        method: `getEvents`,
                        ...getEventsDto,
                    },
                    () => adapter.getEvents(getEventsDto)
                );
            })
        ).then((results) => {
            return results.flat();
        });
    }
}
