import React, { createContext, useEffect, useState, useCallback } from 'react';

/**
 * The events context provider.
 * @param {object} props The events provider props.
 * @returns {JSX.Element} Event context provider component.
 */
export const EventProvider = ({
    children,
    initialGetEventsDto,
    eventService,
}) => {
    const [isLoading, setIsLoading] = useState(false);
    const [isReady, setIsReady] = useState(false);
    const [events, setEvents] = useState([]);

    const getEvents = useCallback((args) => {
        setIsLoading(true);
        return eventService.getEvents(args).then((result) => {
            setEvents(result);
            setIsLoading(false);
        });
    }, []);

    useEffect(() => {
        // Whether to do an initial fetch of event data.
        if (initialGetEventsDto) {
            setIsLoading(true);
            eventService.getEvents(initialGetEventsDto).then((results) => {
                setEvents(results);
                setIsLoading(false);
                setIsReady(true);
            });
        } else {
            setIsLoading(false);
            setIsReady(true);
        }
    }, [setIsLoading, setIsReady]);

    return (
        <EventContext.Provider
            value={{ getEvents, events, eventService, isLoading, isReady }}
        >
            {children}
        </EventContext.Provider>
    );
};

/**
 * The context for user.
 * @export
 */
export const EventContext = createContext({
    events: [],
    eventService: null,
    isLoading: false,
    isReady: false,
    getEvents: null,
});
